import cross from "../../assets/icons/cross.svg";
import close from "../../assets/icons/close.svg";
import style from "./style.css";
const ErrorMessage = ({ message, closeAction }) => {
  return (
    <div class={style.errortoast}>
      <div className={style.errortoastgrid}>
        <div className={style.errortoasticon}>
          <img src={close} alt='' />
        </div>
        <div className={style.errortoastcontent}>
          <p>{message}</p>
        </div>
      </div>
      <div
        className={style.errortoastclose}
        onClick={closeAction}
        role='presentation'
      >
        <img src={cross} alt='' />
      </div>
    </div>
  );
};
export default ErrorMessage;
