import { Router } from "preact-router";
import Home from "./routes/home";
import Results from "./routes/result/result";
import ThankYou from "./routes/thankyou";
import CDHome from "./routes/cd-home";
import "./style";
import "react-phone-input-2/lib/style.css";
import CDHomeVersion from "./routes/cd-home/homeversion";
import CDThankYou from "./components/canada-district/thank-you";

const App = () => {
  // if (typeof window !== "undefined") {
  //   const location = window.location.pathname;
  //   if (location === "/") {
  //     window.location.href = "/lp/en/global/immigration/check/";
  //   }
  // }
  return (
    <div id='app'>
      <main>
        <Router
          onChange={() =>
            typeof window !== "undefined" && window.scrollTo(0, 0)
          }
        >
          <Home path='/lp/en/global/immigration/check/' />
          <ThankYou path='/thank-you/' />
          <Results path='/result/' />
          <CDHome path='/' />
          <CDHomeVersion path='/homeversion/' />
          <CDThankYou path='/thankyou/' />
        </Router>
      </main>
    </div>
  );
};

export default App;
