/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-prototype-builtins */
import { useEffect, useState } from "preact/hooks";
import Select from "react-select";
import backArrow from "../../assets/home/arrow-gray.webp";
import Arrow from "../../assets/home/checkicon.svg";
import result from "../../assets/home/light.webp";
import style from "./style.css";
import { route } from "preact-router";
const newArray = [
  {
    name: "age",
    label: "What is your age?",
    tooltipLabel: "",
    option: [
      { value: "18-20", label: "18-20" },
      { value: "21-25", label: "21-25" },
      { value: "26-30", label: "26-30" },
      { value: "31-35", label: "31-35" },
      { value: "36-39", label: "36-39" },
      { value: "40-49", label: "40-49" },
      { value: "50-59", label: "50-59" },
      { value: "60-69", label: "60-69" },
      { value: "70+", label: "70+" },
    ],
    counter: "2",
  },
  {
    name: "education",
    label: "What is your highest level of education?",
    tooltipLabel: "",
    option: [
      { value: "High School Diploma", label: "High School Diploma" },
      {
        value: "1-Year Program Diploma or Certificate",
        label: "1-Year Program Diploma or Certificate",
      },
      {
        value: "2-Year Program Diploma or Certificate",
        label: "2-Year Program Diploma or Certificate",
      },
      {
        value: "3-Year Program Diploma or Certificate",
        label: "3-Year Program Diploma or Certificate",
      },
      { value: "Bachelor’s Degree", label: "Bachelor’s Degree" },
      { value: "Master’s Degree", label: "Master’s Degree" },
      { value: "Professional Degree", label: "Professional Degree" },
      { value: "PhD", label: "PhD" },
      { value: "Post Doctorate", label: "Post Doctorate" },
    ],
    counter: "3",
  },
  {
    name: "workExperienceInCanada",
    label: "How many years of work experience in Canada?",
    tooltipLabel: "",
    option: [
      { value: "None", label: "None" },
      { value: "1 year", label: "1 year" },
      { value: "2 years", label: "2 years" },
      { value: "3 years", label: "3 years" },
      { value: "4 years", label: "4 years" },
      { value: "5+ years", label: "5+ years" },
    ],
    counter: "4",
  },
  {
    name: "workExperienceOutCanada",
    label: "How many years of work experience outside of Canada?",
    tooltipLabel: "",
    option: [
      { value: "None", label: "None" },
      { value: "1 year", label: "1 year" },
      { value: "2 years", label: "2 years" },
      { value: "3 years", label: "3 years" },
      { value: "4 years", label: "4 years" },
      { value: "5+ years", label: "5+ years" },
    ],
    counter: "5",
  },
  {
    name: "jobOffer",
    label: "Do you have a valid job offer from Canada?",
    tooltipLabel:
      "Getting a job offer from a Registered Canadian employer is not necessarily required.",
    option: [
      { value: "Yes", label: "Yes" },
      { value: "No", label: "No" },
    ],
    counter: "6",
  },
  // {
  //   name: "language",
  //   label: "Do you know Canadian language?",
  //   tooltipLabel: "",
  //   option: [
  //     { value: "Yes", label: "Yes" },
  //     { value: "No", label: "No" },
  //   ],
  //   counter: "7",
  // },
  {
    name: "englishLangSkill",
    label: "English proficiency level",
    tooltipLabel: "",
    option: [
      { value: "Basic", label: "Basic" },
      { value: "Intermediate", label: "Intermediate" },
      { value: "Fluent", label: "Fluent" },
      { value: "Native", label: "Native" },
    ],
    counter: "8",
  },
  {
    name: "frenchLangSkill",
    label: "French proficiency level",
    tooltipLabel: "",
    option: [
      { value: "None", label: "None" },
      { value: "Basic", label: "Basic" },
      { value: "Intermediate", label: "Intermediate" },
      { value: "Fluent", label: "Fluent" },
      {
        value: "Native",
        label: "Native",
      },
    ],
    counter: "9",
  },
  {
    name: "haveRelative",
    label: "Do you have Canadian relatives?",
    tooltipLabel:
      "Please select 'Yes' if you have close family members with Canadian citizenship or permanent residency, such as parents, grandparents, siblings, etc.",
    option: [
      { value: "Yes", label: "Yes" },
      { value: "No", label: "No" },
    ],
    counter: "10",
  },
];
const TopBanner = () => {
  const [values, setValues] = useState({
    age: "",
    education: "",
    workExperienceInCanada: "",
    workExperienceOutCanada: "",
    jobOffer: "",
    frenchLangSkill: "",
    englishLangSkill: "",
    haveRelative: "",
    // language: "",
    defaultValue: {
      label: "",
      value: "",
    },
  });
  const [errors, setErrors] = useState();
  const [formStep, setFormStep] = useState(0);
  const [check, setCheck] = useState(false);
  const [fadeOut, setFadeOut] = useState(false);
  useEffect(() => {
    if (Object.values(values).every((val) => val !== "")) {
      localStorage.setItem("result", JSON.stringify(values));
      setCheck(true);
    }
  }, [values]);

  const prevFormStep = () => {
    setCheck(false);
    setFadeOut(true);
    setTimeout(() => {
      setFormStep((currentStep) => Math.abs(currentStep - 1));
      setFadeOut(false);
    }, [300]);
  };

  const handleOnBlur = (val, name) => {
    if (values[name] === "") {
      setErrors({ [name]: true });
    } else if (val.value !== "") {
      setErrors({ [name]: false });
    }
  };

  const handleInputChange = (val, name, counter) => {
    setValues({
      ...values,
      [name]: val.value,
      defaultValue: { label: val.value, value: val.value },
    });
    if (counter === "2") {
      window.gtag("event", "form_start", {
        form_id: name,
        form_name: name,
        form_destination: window.location.href,
      });
    }
    if (counter !== "10") {
      setTimeout(() => {
        setFormStep((currentStep) => currentStep + 1);
      }, 200);
    }
    if (counter === "10") {
      return setFadeOut(false);
    }
    setFadeOut(true);
    setTimeout(() => {
      setFadeOut(false);
    }, [300]);
    setErrors({ [name]: false });
  };

  const handleCheckOption = async() => {
   await window.gtag("event", "form_submit", {
    form_id: document.getElementsByTagName("form").QA_form.id,
    form_name:document.getElementsByTagName("form").QA_form.name,
    form_destination:document.getElementsByTagName("form").QA_form.baseURI
  });
    if (Object.values(values).every((val) => val !== "")) {
      route("/result/");
    }
  };

  const customStyles = {
    option: (provided) => ({
      ...provided,
      background: "#fff",
      color: "#333",
      fontWeight: "500",
      fontSize: "16px",
      padding: "4px 10px",
      lineHeight: "24px",
      cursor: "pointer",
      ":hover": {
        background: "#F1EEFB",
        borderRadius: 8,
      },
    }),
    menu: (provided) => ({
      ...provided,
      background: "#ffffff",
      padding: "15px",
    }),
    control: () => ({
      background: "#ffffff",
      border: "2px solid #CED0D0",
      borderRadius: "6px",
      padding: "0px 10px",
      color: "#333333",
      display: "flex",
      alignItem: "center",
      height: "48px",
      fontWeight: "500",
      fontSize: "16px",
      lineHeight: "24px",
    }),
    singleValue: (provided) => ({
      ...provided,
      color: "#333",
      fontWeight: "500",
      fontSize: "16px",
      lineHeight: "24px",
    }),
    errorStyle: (provided) => ({
      ...provided,
      border: "2px solid red",
    }),
  };
  return (
    <div class={style.topBanner}>
      <div class={style.topBannerContainer}>
        <div class={style.topBannerTitle}>
          <h3>
            Check if You Can Immigrate to <span>Canada</span>
          </h3>
          <p>
            Get a full list of eligible immigration programs based on your
            profile.
          </p>
        </div>

        <div class={style.formGrid} id='instantForm'>
          <h4>
            Complete the Form for <span>Instant Results</span>{" "}
            <img src={result} alt='' />
          </h4>
          <div class={style.formBox}>
            <ul>
              <li class={`${formStep >= 0 ? style.active : ""}`}>
                <AgeIcon />
              </li>
              <li class={`${formStep >= 1 ? style.active : ""}`}>
                <EducationIcon />
              </li>
              <li
                class={`${formStep >= 2 || formStep >= 3 ? style.active : ""}`}
              >
                <ExperienceIcon />
              </li>
              <li
                class={`${formStep >= 3 || formStep >= 4 ? style.active : ""}`}
              >
                <ExperienceWorld />
              </li>
              <li
                class={`${formStep >= 4 || formStep >= 5 ? style.active : ""}`}
              >
                <HandIcon />
              </li>
              <li
                class={`${formStep >= 5 || formStep >= 6 ? style.active : ""}`}
              >
                <LanguageEnIcon />
              </li>
              <li
                class={`${formStep >= 6 || formStep >= 7 ? style.active : ""}`}
              >
                <LanguagefrIcon />
              </li>
              <li
                class={`${formStep >= 7 || formStep >= 8 ? style.active : ""}`}
              >
                <RelativesIcon />
              </li>
            </ul>
            <form id='QA_form_id' name='QA_form'>
              <div
                class={`${style.formScroll} ${
                  fadeOut ? style.formFadeOut : style.formFadeIn
                }`}
              >
                {newArray.slice(formStep, formStep + 1).map((el) => {
                  return (values[el.name] === "" && el?.counter !== "10") ||
                    (values[el.name] && el?.counter !== "10") ? (
                    <div class={style.formField} key={el.name}>
                      <SelectInput
                        fadeOut={fadeOut}
                        label={el.label}
                        tooltipLabel={el.tooltipLabel}
                        options={el.option}
                        name={el.name}
                        errors={errors}
                        onChange={handleInputChange}
                        onBlur={handleOnBlur}
                        counter={el.counter}
                        customStyles={customStyles}
                        values={values}
                        defaultVal={{
                          label: values[el.name] || "Select",
                          value: values[el.name] || "",
                        }}
                      />
                    </div>
                  ) : (
                    <div class={style.formField} key={el.name}>
                      <SelectInput
                        fadeOut={fadeOut}
                        label={el.label}
                        tooltipLabel={el.tooltipLabel}
                        options={el.option}
                        name={el.name}
                        errors={errors}
                        onChange={handleInputChange}
                        onBlur={handleOnBlur}
                        counter={el.counter}
                        customStyles={customStyles}
                        values={values}
                        defaultVal={{
                          label: values[el.name] || "Select",
                          value: values[el.name] || "",
                        }}
                      />
                    </div>
                  );
                })}
              </div>
              <div class={style.buttonGrid}>
                {formStep > 0 && (
                  <button
                    onClick={prevFormStep}
                    type='button'
                    class={style.backBtn}
                  >
                    <img src={backArrow} alt='' />
                    Back
                  </button>
                )}
                {check ? (
                  <button
                    class={style.submitBtn}
                    type='button'
                    onClick={handleCheckOption}
                  >
                    Check my options
                    <img src={Arrow} alt='' />
                  </button>
                ) : null}
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};
export default TopBanner;

const SelectInput = ({
  label,
  options,
  name,
  errors,
  onChange,
  onBlur,
  counter,
  customStyles,
  disable,
  tooltipLabel,
  values,
  defaultVal,
  fadeOut = { fadeOut },
}) => {
  return (
    <div class={`${style.formGroup}`}>
      <label
        class={`${fadeOut ? style.formFadeOutLeft : style.formFadeInLeft}`}
        htmlFor={`react-select-${counter}-input`}
      >
        {label}
        {tooltipLabel !== "" && (
          <div class={style.formInfoIcon}>
            <InfoIcon />
            <span class={style.formInfoTooltip}>{tooltipLabel}</span>
          </div>
        )}
      </label>
      <Select
        styles={customStyles}
        inputProps={{ id: name }}
        options={options}
        defaultValue={defaultVal}
        value={defaultVal}
        isDisabled={disable}
        isSearchable={false}
        onChange={(e) => onChange(e, name, counter)}
        onBlur={(e) => onBlur(e, name)}
        className={`react-select-container ${
          errors?.[name] && values[name] === "" ? "error-msg" : ""
        }`}
        classNamePrefix='react-select'
      />
    </div>
  );
};

const AgeIcon = () => {
  return (
    <svg
      width='45'
      height='44'
      viewBox='0 0 45 44'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
    >
      <path
        d='M33.2228 33.1563H12.9183C12.6746 33.1563 12.4409 33.0595 12.2686 32.8872C12.0963 32.7149 11.9995 32.4812 11.9995 32.2376C11.9995 26.9216 15.2262 23.4855 20.2196 23.4855H25.9214C30.9158 23.4855 34.1425 26.9216 34.1425 32.2376C34.1425 32.3583 34.1187 32.4778 34.0724 32.5894C34.0262 32.7009 33.9585 32.8022 33.8731 32.8875C33.7877 32.9729 33.6862 33.0405 33.5746 33.0866C33.4631 33.1328 33.3435 33.1564 33.2228 33.1563ZM13.8738 31.3188H32.2664C31.9476 27.5445 29.6268 25.323 25.9205 25.323H20.2187C16.5134 25.323 14.1953 27.5473 13.8738 31.3188Z'
        fill='#6243CE'
      />
      <path
        d='M23.0905 20.7775C20.0678 20.7775 18.0374 18.4108 18.0374 14.8883C18.0374 11.3658 20.0678 9 23.0905 9C26.1132 9 28.1437 11.3658 28.1437 14.8883C28.1437 18.4108 26.1132 20.7775 23.0905 20.7775ZM23.0905 10.8375C20.7201 10.8375 19.8749 12.9304 19.8749 14.8883C19.8749 16.8462 20.7201 18.94 23.0905 18.94C25.4609 18.94 26.3062 16.8471 26.3062 14.8883C26.3062 12.9295 25.4609 10.8375 23.0905 10.8375Z'
        fill='#6243CE'
      />
    </svg>
  );
};

