import Layout from "../../components/layout";
import bell from "../../assets/results/bell.webp";
import Sidebar from "../../components/results/sidebar";
import Report from "../../components/results/report";
import ResultForm from "../../components/results/form";
import style from "./style.css";
//import { useEffect } from "preact/hooks";

const Results = () => {
  const handleClickScroll = (id) => {
    const element = document.getElementById("form-result");
    window.gtag("event", "select_content", { item_id: id });
    if (element) {
      // 👇 Will scroll smoothly to the top of the next section
      element.scrollIntoView({ behavior: "smooth" });
    }
  };
// useEffect(()=>{
// window.addEventListener("scroll",()=>console.log(window.scrollY))
// return()=>{
//   window.removeEventListener("scroll",console.log("helll"))
// }
// },[])
  return (
    <Layout>
      <div class={style.results} id="result">
        <div class={style.container}>
          <div class={style.pageTitle}>
            <h2>
              Your results <img src={bell} alt='bell' />
            </h2>
            <p>
              Your profile analysis is complete! Please find your results below.
            </p>
          </div>
          <div class={style.resultGrid}>
            <Sidebar result={JSON.parse(localStorage.getItem("result"))} />
            <Report handleClickScroll={handleClickScroll} />
          </div>
          <div class={style.callToForm} id='form-result'>
            <ResultForm />
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Results;
