
import thankyou from "../../../assets/thankyou/thankyou.webp"
import CDLayout from "../layout";
import style from './style.css';


const CDThankYou = () => (
	<>
    <CDLayout>
    <div class={style.thankyou}>
	    <div class={style.container}>
			<img src={thankyou} alt="thankyou" />
			<h2>Thank You!</h2>
			<p>An immigration professional will contact you shortly to complete your profile evaluation and discuss the next steps.</p>
		</div>
	</div>

    </CDLayout>
	
   
    </>
);

export default CDThankYou;
