import style from "./style.css";
import calltoaction from "../../assets/home/calltoaction.mp4";

const CallToAction = () => {
  const handleScroll = () => {
    if (typeof window !== "undefined" && typeof document !== "undefined") {
      const position = document.getElementById("instantForm");
      position.scrollIntoView({ behavior: "smooth", block: "center" });
    }
  };

  return (
    <div class={style.callToAction}>
      <div class={style.callToActionContainer}>
        <div class={style.callToActionGrid}>
          <div class={style.callToActionCover}>
            {/* <img src={calltoaction} alt="img" width={393} height={296} />
            <div className={style.typoAnimate}>
              {/* <div class="typewrite" data-period="2000" data-type='[ "Express Entry", "Provincial Nominee", "Atlantic Immigration", "Temporary Foreign Worker", "Federal Skilled Worker", "Student", "Working Holiday" ]'>
                        <span class="wrap" />
                        </div> 
            </div> */}

           <video width="296" height="296" autoplay muted>
           <source src={calltoaction} type="video/mp4" />
           </video>
          </div>

          <div class={style.callToActionContent}>
            <h2>
              Discover Your Options within <span>2 minutes!</span>
            </h2>
            <span onClick={() => handleScroll()} role="presentation">
              Go to Form
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};
export default CallToAction;
