import { Link } from "preact-router/match";
import check from "../../assets/results/check.webp";
import style from "./style.css";

const Sidebar = ({ result }) => (
  <div class={style.sidebar}>
    <div class={style.sidebarGrid}>
      <p>Profile Summary</p>
      <ul>
        <li>
          <span>
            Age <img src={check} alt='check' />
          </span>
          <b>
            {result?.age || 18} years old <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            Education <img src={check} alt='check' />
          </span>
          <b>
            {result?.education || "High school diploma"}{" "}
            <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            Work experience in Canada <img src={check} alt='check' />
          </span>
          <b>
            <div class={style.desktopview}> {result?.workExperienceInCanada || "2 years"}{" "} </div>
            <div class={style.mobileview}>{result?.workExperienceInCanada ==='None' ? '0 year':result?.workExperienceInCanada} of work experience in Canada</div>
            <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            Work experience outside of Canada <img src={check} alt='check' />
          </span>
          <b>
          <div class={style.desktopview}> {result?.workExperienceOutCanada || "2 years"}{" "} </div>
            <div class={style.mobileview}>
            {result?.workExperienceOutCanada ==='None' ? '0 year':result?.workExperienceOutCanada}{" "} of work experience outside of Canada
            </div>
            <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            Job offer <img src={check} alt='check' />
          </span>
          <b>
            {result.jobOffer === "Yes"
              ? "I have a valid job offer from a registered Canadian employer"
              : "I don't have a valid job offer from a registered Canadian employer"}{" "}
            <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            English proficiency level <img src={check} alt='check' />
          </span>
          <b>
            <div class={style.desktopview}>{result?.englishLangSkill}</div>
            <div class={style.mobileview}>English level: {result?.englishLangSkill?.toLowerCase()}</div>
             <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            French proficiency level <img src={check} alt='check' />
          </span>
          <b>
            <div class={style.desktopview}>{result?.frenchLangSkill}</div>
            <div class={style.mobileview}>French level: {result?.frenchLangSkill?.toLowerCase()}</div>
             <img src={check} alt='check' />
          </b>
        </li>
        <li>
          <span>
            Canadian relatives <img src={check} alt='check' />
          </span>
          <b>
            {result?.haveRelative === "Yes"
              ? "I have relatives in Canada with Canadian citizenship or permanent residency"
              : "I don't have relatives in Canada with Canadian citizenship or permanent residency"}{" "}
            <img src={check} alt='check' />
          </b>
        </li>
      </ul>
      <div class={style.resubmit}>
        <Link href='/lp/en/global/immigration/check/'>Resubmit</Link>
      </div>
    </div>
  </div>
);

export default Sidebar;
