import { useState } from 'preact/hooks'
import { Link } from 'preact-router/match'
import logo from '../../../assets/header/cdlogo.svg'
import menu from '../../../assets/cdimages/menucd.svg'
import close from '../../../assets/cdimages/closecd.svg'

import cdstyle from './style.css'

const scrollTowhat = () => {
  let What = document.getElementById('What')
  if (What) {
    What.scrollIntoView()
  } else {
    window.location.href = '#What'
  }
}

const scrollToHow = () => {
  let How = document.getElementById('How')
  if (How) {
    How.scrollIntoView()
  } else {
    window.location.href = '#How'
  }
}

const scrollToWhy = () => {
  let Why = document.getElementById('Why')
  if (Why) {
    Why.scrollIntoView()
  } else {
    window.location.href = '#Why'
  }
}

// eslint-disable-next-line react-hooks/rules-of-hooks

const CDHeader = () => {
 
  const thankyouUrl =typeof window !== "undefined"&& window.location.pathname === '/thankyou'
  const [isActive, setActive] = useState(false)
  const toggleClass = () => {
    setActive(!isActive)
  }
  return (
    <div class={cdstyle.cdheader}>
      <div class={cdstyle.cdcontainer}>
        <div class={cdstyle.cdheaderContent}>
          <Link to="/homeversion">
            <img src={logo} alt="logo" width={80} height={32} />
          </Link>
          {!thankyouUrl ? (
            <>
              <div class={cdstyle.cdheaderRight}>
                <ul>
                  <li onClick={scrollTowhat}>What</li>
                  <li onClick={scrollToHow}>How</li>
                  <li onClick={scrollToWhy}>Why</li>
                </ul>
              </div>
              <div class={cdstyle.cdheaderRightToggle}>
                <div class={cdstyle.cdToggleButton} onClick={toggleClass}>
                  {isActive ? (
                    <div>
                      <img src={close} alt="close" />
                    </div>
                  ) : (
                    <div>
                      <img src={menu} alt="menu" />
                    </div>
                  )}
                </div>
                {isActive ? (
                  <div class={cdstyle.cdheaderRightItem}>
                    <ul>
                      <li onClick={scrollTowhat}>What</li>
                      <li onClick={scrollToHow}>How</li>
                      <li onClick={scrollToWhy}>Why</li>
                    </ul>
                  </div>
                ) : (
                  ''
                )}
              </div>
            </>
          ) : (
            ''
          )}
        </div>
      </div>
    </div>
  )
}

export default CDHeader
